import {Component, OnDestroy, OnInit} from '@angular/core';

import {Observable, Subject, Subscription} from 'rxjs';

import {debounceTime, distinctUntilChanged, filter, switchMap} from 'rxjs/operators';

import { Artitle } from '../model/artitle';
import { ArticleService } from '../services/article.service';
import { NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'app-article-search',
  templateUrl: './article-search.component.html',
  styleUrls: ['./article-search.component.css']
})
export class ArticleSearchComponent implements OnInit, OnDestroy {

  articlesObservable: Observable<Artitle[]>;
  private subbed: Subscription;
  private searchTerms = new Subject<string>();
  searchField: string;

  constructor(
    private articleService: ArticleService,
    private router: Router
  ) {
    this.init();
    this.subbed = router.events
      .pipe(filter(e => e instanceof NavigationEnd))
      .subscribe(() => this.init());
  }

  ngOnInit(): void { }

  ngOnDestroy() {
    this.subbed.unsubscribe();
  }

  // Put a search term on the observable stream
  search(term: string): void {
    this.searchTerms.next(term);
  }

  nav(id): void {
    this.searchField = '';
    this.router.navigate(['/detail/' + id]);
    this.search('');
  }

  init(): void {
    this.articlesObservable = this.searchTerms.pipe(
      // Wait 300ms after each keystroke
      debounceTime(300),

      // Ignore new term if same as previous
      distinctUntilChanged(),

      // Switch to new search observable each time the term changes
      switchMap((term: string) =>
        this.articleService.searchArticleTitles(term)
      )
    );
  }

}
