import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Article } from '../model/article';
import { Artitle } from '../model/artitle';
import { MessageService } from './message.service';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import {catchError, map, tap} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class ArticleService {

  private dbUrl = 'https://raw.githubusercontent.com/madblade/folio-db/master/db/';
  private articlesUrl = 'all-titles.json';
  private categoriesUrl = 'categories.json';

  constructor(
    private http: HttpClient,
    private messageService: MessageService
  ) {}

  getCategories(): Observable<string[][]> {
    const url = this.dbUrl + this.categoriesUrl;
    return this.http.get<string[][]>(url)
      .pipe(
        tap(() => this.log('fetched categories')),
        catchError(this.handleError('getCategories', [])));
  }

  getArticles(cat: string): Observable<Artitle[]> {
    const url = this.dbUrl + cat + '.json';
    return this.http.get<Artitle[]>(url)
      .pipe(
        tap(() => this.log('fetched articles')),
        catchError(this.handleError('getArticles', []))
      );
  }

  getArticle(id: number): Observable<Article> {
    const url = this.dbUrl + 'articles/' + id + '.json';
    return this.http.get<Article>(url)
      .pipe(
        tap(() => this.log(`fetched article ${id}`)),
        catchError(this.handleError<Article>('getArticle id=${id}'))
      );
  }

  // GET articles whose name contains search term
  searchArticleTitles(term: string): Observable<Artitle[]> {
    if (!term.trim()) {
      return of([]); // Empty article array
    }

    const url = this.dbUrl + this.articlesUrl;
    return this.http.get<Artitle[]>(url)
      .pipe(
        map((articles) => {
          const filtered = [];
          for (let i = 0; i < articles.length; ++i) {
            if (articles[i].title.toLowerCase().includes(term.toLowerCase())) {
              filtered.push(articles[i]);
            }
          }
          return filtered;
        }),
        catchError(this.handleError<Article[]>('searchArticles', []))
      )
      .pipe(
        tap(() => this.log(`found articles matching "${term}"`)),
        catchError(this.handleError<Article[]>('searchArticles', []))
      );
  }

  private log(message: string) {
    this.messageService.add(`ArticleService: ${message}`);
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // [Enhancement] Should send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // Should transform error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // [Enhancement] Let the app keep running by returning an empty result
      return of(result as T);
    };
  }

}
